    overflow: hidden;
    height: 40px;
    width: 100%;
  }
  #bond > svg {
    display: block;
    width: 7px;
    height: 7px;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  #bond:hover > svg {
    fill: #1a7090;
  }
  #bond.opened {
    overflow: visible;
  }
}
@media (max-height: 700px) and (max-width: 850px) {
  li.opened li {
    display: flex;
  }
  li.opened li menu {
    width: 100%;
  }
  li.opened button {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 1px 0;
    padding: 0.3em 0.3em 0.3em 2em;
    height: 1.7em;
    font-size: 12px;
    text-align: left;
  }
  li.opened button:before,
  li.opened button:after {
    padding-right: 0.4em;
    font-size: 16px;
  }
  li.opened button[disabled] {
    background: inherit;
    color: #baa;
  }
  li.opened button kbd {
    margin: 2.5px 0 0 10px;
    display: inline-block;
  }
  li.opened button > svg {
    width: 17px;
    height: 17px;
  }
  li.opened button:hover {
    transition: transform, 0.2s, ease-out;
    transform: scale(none);
    transform: scale(1);
    background-color: #eee;
  }
  li.opened .selected button {
    transition: transform, 0.2s, ease-out;
    transform: scale(none);
    transform: scale(1);
    background: #444;
    color: white;
  }
  li.opened .selected button > span {
    color: white;
  }
  li.opened .selected button > svg {
    fill: white;
    stroke: white;
    stroke-width: 5;
  }
}
@media (max-height: 700px) and (max-width: 1040px) {
  #bond {
    height: 32px;
    width: 100%;
  }
}
@media (max-height: 600px) {
  #rgroup,
  #transform,
  #bond {
    height: 32px;
    width: 100%;
  }
}
@media (min-height: 800px) {
  #reaction + li {
    margin-top: 5px;
    border-top: 1px solid #ccc;
    padding-top: 4px;
  }
}
@media (min-height: 650px) {
  #erase + li,
  #chain + li,
  #charge + li,
  #transform + li,
  #atom + li,
  #freq-atoms + li {
