}
@media (min-width: 1160px) {
  #analyse + li,
  #document + li,
  #edit + li,
  #zoom + li {
    margin-left: 10px;
    border-left: 1px solid #ccc;
    padding-left: 9px;
  }
}
@media (max-height: 600px), (max-width: 1040px) {
  body,
  select,
  form {
    font-size: 11px;
  }
  #canvas {
    top: 37px;
    left: 37px;
    right: 37px;
    bottom: 37px;
  }
  #canvas .measure-log {
    top: 37px;
    right: 37px;
  }
  #mainmenu,
  #template {
    left: 37px;
    right: 37px;
    height: 32px;
  }
  #elements,
  #toolbox {
    top: 37px;
    bottom: 37px;
    width: 32px;
  }
  [role='toolbar'] button {
    width: 24px;
    height: 24px;
    margin: 4px;
  }
  [role='toolbar'] button > svg {
    position: absolute;
    top: 0.1em;
    left: 0.1em;
    font-size: 20px;
    width: 20px;
    height: 20px;
    fill: #333;
  }
  #chiral-flag button {
    width: 48px;
    margin-left: 8px;
    margin-right: 8px;
  }
  #chiral-flag button > svg {
    position: static;
    left: 0;
    top: 0;
    width: 40px;
  }
  #select,
  #reaction {
    height: 32px;
    width: 100%;
  }
  li.opened > menu {
    left: 37px;
  }
  #atom button,
  #freq-atoms button {
    font-size: 16px;
  }
}
@media (max-width: 770px) {
